import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./login.css";

function Login() {
  const navigate = useNavigate();

  const [user, setUser] = useState({
    firstName: "",
    lastName: "",
    userName: "",
    email: "",
    age: "",
    contactNumber: "",
    password: "",
  });

  const [currentPage, setCurrentPage] = useState("login");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [disable, setDisable] = useState(true);
  const [RegDisable, setRegDisable] = useState(true);

  const isValidUserEmail =
    /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
      email
    );
  const isValidRegUserName = /^[a-zA-Z0-9._-]{3,15}$/.test(user.userName);
  const isValidEmail =
    /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
      user.email
    );
  const isValidContactNumber =
    /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/.test(
      user.contactNumber
    );
  const isValidAge = /^(0?[1-9]|[1-9][0-9]|[1][1-9][1-9]|200)$/.test(user.age);
  const isValidFirstName = /^[a-zA-Z ]+$/.test(user.firstName);
  const isValidLastName = /^[a-zA-Z ]+$/.test(user.lastName);

  useEffect(() => {
    email === "" || password === "" || !isValidUserEmail || password.length < 5
      ? setDisable(true)
      : setDisable(false);
  }, [email, password, isValidUserEmail, password.length]);

  useEffect(() => {
    user.userName === "" ||
    user.password === "" ||
    user.email === "" ||
    user.contactNumber === "" ||
    user.firstName === "" ||
    user.lastName === "" ||
    user.age === "" ||
    !isValidRegUserName ||
    !isValidFirstName ||
    !isValidLastName ||
    !isValidEmail ||
    !isValidContactNumber ||
    !isValidAge ||
    user.password.length < 5
      ? setRegDisable(true)
      : setRegDisable(false);
  }, [
    user.userName,
    user.password,
    user.email,
    user.contactNumber,
    user.firstName,
    user.lastName,
    user.age,
    isValidRegUserName,
    isValidFirstName,
    isValidLastName,
    isValidEmail,
    isValidContactNumber,
    isValidAge,
    user.password.length,
  ]);

  let name, value;

  const handleInputs = (event) => {
    name = event.target.name;
    value = event.target.value;

    setUser({ ...user, [name]: value });
  };

  useEffect(() => {
    if (localStorage.getItem("user")) {
      navigate("/Profile");
    }
  });

  const postData = async (page, e) => {
    e.preventDefault();

    setCurrentPage(page);

    const {
      firstName,
      lastName,
      userName,
      email,
      age,
      contactNumber,
      password,
    } = user;

    const res = await fetch("/api/register", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        firstName,
        lastName,
        userName,
        email,
        age,
        contactNumber,
        password,
      }),
    });

    const data = await res.json();

    if (res.status === 422 || !data) {
      window.alert("Registration Failed");
      console.log("Invalid Registration");
    } else {
      window.alert("Registration Successful");
      console.log("Successful Registration");
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (currentPage === "login") {
      console.log(
        "Logging in with username:",
        email,
        "and password:",
        password
      );
    } else {
      console.log(
        "Registering with email:",
        user.email,
        "and password:",
        user.password
      );
    }
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const handlingProfile = async (e) => {
    e.preventDefault();
    setDisable(true);
    const res = await fetch("/api/login", {
      method: "POST",
      credentials: "include",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email,
        password,
      }),
    });
    const data = await res.json();

    if (res.status === 400 || !data) {
      window.alert("Invalid Credentials");
    } else {
      window.alert("Login Successful");
      localStorage.setItem("user", email);
      navigate("/profile");
    }
    setDisable(false);
  };

  return (
    <div className="wrapper bg-dark d-flex align-items-center justify-content-center w-100">
      <div className="login">
        {currentPage === "login" ? (
          <>
            <h2 className="mb-3">Login</h2>

            <form onSubmit={handleSubmit} method="POST">
              <div className="form-group was-validated mb-2">
                <label htmlFor="text" className="form-label">
                  Email
                </label>
                <input
                  className="form-control"
                  type="email"
                  placeholder="Enter email"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)}
                  required
                  pattern="^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$"
                ></input>
                <div className="invalid-feedback">
                  Please provide valid email
                </div>
              </div>
              <div className="form-group was-validated mb-2">
                <label htmlFor="password" className="form-label">
                  Password
                </label>
                <input
                  className="form-control"
                  type="password"
                  placeholder="Enter password"
                  value={password}
                  onChange={(event) => setPassword(event.target.value)}
                  required
                  minLength={5}
                ></input>
                <div className="invalid-feedback">
                  Password must contain atleast 5 letters
                </div>
              </div>
              <button
                type="submit"
                className="btn btn-success block w-100"
                onClick={handlingProfile}
                disabled={disable}
              >
                Login
              </button>
              <p>
                Don't have an account?{" "}
                <button
                  type="submit"
                  className="btn btn-primary mt-3"
                  onClick={() => handlePageChange("register")}
                >
                  Register
                </button>
              </p>
            </form>
          </>
        ) : (
          <>
            <h2 className="mb-3">Register</h2>
            <form onSubmit={handleSubmit} method="POST">
              <div className="form-group was-validated mb-2">
                <label htmlFor="text" className="form-label">
                  First Name
                </label>
                <input
                  className="form-control"
                  name="firstName"
                  type="text"
                  placeholder="Enter first name"
                  value={user.firstName}
                  onChange={handleInputs}
                  required
                  pattern="^[a-zA-Z ]+$"
                />
              </div>

              <div className="form-group was-validated mb-2">
                <label htmlFor="text" className="form-label">
                  Last Name
                </label>
                <input
                  className="form-control"
                  name="lastName"
                  type="text"
                  placeholder="Enter last name"
                  value={user.lastName}
                  onChange={handleInputs}
                  required
                  pattern="^[a-zA-Z ]+$"
                />
              </div>

              <div className="form-group was-validated mb-2">
                <label htmlFor="text" className="form-label">
                  User Name
                </label>
                <input
                  className="form-control"
                  name="userName"
                  type="text"
                  placeholder="Enter user name"
                  value={user.userName}
                  onChange={handleInputs}
                  required
                  minLength={5}
                  pattern="^[a-zA-Z0-9._-]{3,15}$"
                />
                <div className="invalid-feedback">User Name not available</div>
              </div>

              <div className="form-group was-validated mb-2">
                <label htmlFor="email" className="form-label">
                  Email Address
                </label>
                <input
                  className="form-control"
                  name="email"
                  type="email"
                  placeholder="Enter email"
                  value={user.email}
                  onChange={handleInputs}
                  required
                  pattern="^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$"
                />
              </div>

              <div className="form-group was-validated mb-2">
                <label htmlFor="text" className="form-label">
                  Age
                </label>
                <input
                  className="form-control"
                  name="age"
                  type="text"
                  placeholder="Enter age"
                  value={user.age}
                  onChange={handleInputs}
                  required
                  pattern="^(0?[1-9]|[1-9][0-9]|[1][1-9][1-9]|200)$"
                />
              </div>

              <div className="form-group was-validated mb-2">
                <label htmlFor="text" className="form-label">
                  Contact Number
                </label>
                <input
                  className="form-control"
                  name="contactNumber"
                  type="text"
                  placeholder="Enter contact number"
                  value={user.contactNumber}
                  onChange={handleInputs}
                  required
                  pattern="^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$"
                />
              </div>

              <div className="form-group was-validated mb-2">
                <label htmlFor="password" className="form-label">
                  Password
                </label>
                <input
                  className="form-control"
                  name="password"
                  type="password"
                  placeholder="Enter password"
                  value={user.password}
                  onChange={handleInputs}
                  required
                  minLength={5}
                />
                <div className="invalid-feedback">
                  Password must contain atleast 5 letters
                </div>
              </div>

              <button
                type="submit"
                className="btn btn-success block w-100"
                onClick={(e) => postData("login", e)}
                disabled={RegDisable}
              >
                Register
              </button>
              <p>
                Already have an account?{" "}
                <button
                  type="submit"
                  className="btn btn-primary mt-3"
                  onClick={() => handlePageChange("login")}
                >
                  Login
                </button>
              </p>
            </form>
          </>
        )}
      </div>
    </div>
  );
}

export default Login;
