import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import "./home.css"

function Home() {
    const navigate = useNavigate();
    useEffect(() => {
        if (localStorage.getItem("user")) {
            navigate("/Profile");
        }
    })
    const gologin = () =>{
        navigate('/login');
    }
       return (
        < div className='homebody'>
            <p><h3>Welcome to</h3></p>
            <h1 className='h11'><b>Greddiit</b></h1>
            <p><button className='black' onClick={gologin}>Let's Get Started{'   '}{'  '}<i className='fas fa-arrow-right'></i></button></p>
        </div >
    )
}

export default Home;