import React from "react";
import { useNavigate } from "react-router-dom";
import { useEffect } from "react";
import "./mysubgreddiits.css";
import { useState } from "react";
import axios from "axios";

function Mysubgreddiits(props) {
  const [loaded, setLoaded] = useState(false);
  const [userData, setUserData] = useState({});
  const [subGreddiits, setSubGreddiits] = useState([]);

  useEffect(() => {
    axios
      .get("/api/fsubgreddiits", {withCredentials: true})
      .then((res) => setSubGreddiits(res.data))
      .catch((err) => console.error(err));
  }, []);

  const navigate = useNavigate();

  const callSubgreddiitsPage = async () => {
    try {
      const res = await fetch("/api/subgreddiits", {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        credentials: "include",
      });

      const data = await res.json();
      // console.log(data);
      setUserData(data);

      if (!res.status === 200) {
        const error = new Error(res.error);
        throw error;
      }
      setLoaded(true);
    } catch (e) {
      console.log(e);
      navigate("/login");
    }
  };

  const Open = async (subgreddiitId, e) => {
    e.preventDefault();

    navigate(`/mysubgreddiits/${subgreddiitId}`);
  };

  const Delete = async (subGreddiitID, e) => {
    e.preventDefault();

    const res = await fetch("/api/subgreddiits", {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        subGreddiitID,
      }),
    });

    const data = await res.json();

    if (!res.status === 201 || !data) {
      window.alert("Deletion Failed");
      console.log("Invalid Deletion");
    } else {
      // window.alert("Deletion Successful");
      console.log("Successful Deletion");
      setSubGreddiits({
        subGreddiits: subGreddiits?.filter((s) => s._id !== subGreddiitID),
      });
      // window.location.reload();
      navigate("/mysubgreddiits");
    }
  };

  const createSubGreddiit = () => {
    navigate("/createsubgreddiit");
  };

  useEffect(() => {
    callSubgreddiitsPage();
  }, []);

  useEffect(() => {
    if (!localStorage.getItem("user")) {
      props.setflag(false);
      navigate("/login");
    } else {
      props.setflag(true);
    }
  }, []);
  if (!loaded)
    return (
      <h1>
        Loading... <i class="fas fa-sync-alt fa-spin"></i>
      </h1>
    );
  else
    return (
      <table className="table table-bordered table-striped">
        <thead>
          <tr>
            <th>My SubGreddiits List</th>
            <th>
              <button className="btn btn-primary" onClick={createSubGreddiit}>
                <i className="fas fa-plus"></i> Create New
              </button>
            </th>
          </tr>
        </thead>
        <tbody>
          {subGreddiits?.map((subGreddiit) => {
            const yourSubGreddiit = subGreddiit?.owner === userData._id;
            return (
              <tr key={subGreddiit._id}>
                {!yourSubGreddiit ? (
                  <></>
                ) : (
                  <>
                    <td>
                      <i className="fas fa-file-alt"></i> {subGreddiit?.name}
                      <p></p>
                      <p>Description : {subGreddiit?.description}</p>
                      <p>No. of People : {subGreddiit?.followers.length}</p>
                      <p>No. of Posts : {subGreddiit?.posts.length}</p>
                      <p>Created on: {new Date(subGreddiit?.creationDate).toLocaleDateString()}</p>
                      <p>
                        Tags :{" "}
                        {subGreddiit.tags.map((tag) => tag.tag).join(", ")}
                      </p>
                      <p>
                        Banned KeyWords :{" "}
                        {subGreddiit.bannedKeyWords
                          .map((keyword) => keyword.keyword)
                          .join(", ")}
                      </p>
                    </td>
                    <td>
                      {" "}
                      <button
                        onClick={(e) => Open(subGreddiit._id, e)}
                        className="green"
                        style={{ fontSize: "13px" }}
                      >
                        <i
                          className="fas fa-user-plus"
                          style={{ color: "orange", fontSize: "13px" }}
                        ></i>
                        {"  "}Open
                      </button>
                    </td>
                    <td>
                      {" "}
                      <button
                        onClick={(e) => Delete(subGreddiit._id, e)}
                        className="red"
                        style={{ fontSize: "13px" }}
                      >
                        <i
                          className="fas fa-user-plus"
                          style={{ color: "black", fontSize: "13px" }}
                        ></i>
                        {"  "}Delete
                      </button>
                    </td>
                  </>
                )}
              </tr>
            );
          })}
        </tbody>
      </table>
    );
}

export default Mysubgreddiits;
